// Game.tsx
import React, {  useEffect, useRef, useState } from 'react';
import { Board } from './components/board';
import { DealButton, NoDealButton, SeeOptionsButton } from './components/dealOrNoDealButton';
import { Result } from './components/result';
import { imagesPrizes } from './data/imagesPrizes';
import { shuffle } from 'lodash';

const openChestSound = require('./assets/open_chest.wav')

export interface CaseInteface {
  value: number
  selected: boolean
  prize: string
}


const App: React.FC = () => {
  const audioRef = useRef<HTMLAudioElement | null>(null);
  const shuffledPrizes = shuffle(imagesPrizes);
  
  let casesList = Array.from({ length: 13 }, (_, i) => ({ value: i + 1, selected: false, prize : shuffledPrizes.pop() as string}));
  const [cases, setCases] = useState<CaseInteface[]>(casesList);

  const [playerCase, setPlayerCase] = useState<number | null>(null);
  const [bankersOffer, setBankersOffer] = useState<number | null>(null);
  const [gameOver, setGameOver] = useState<boolean>(false);

  const [avalableOptions, setShowAvailableOpts] = useState<boolean>(false);

  useEffect(() => {
    if (audioRef.current) {
      audioRef.current.play();
    }
  }, [audioRef]);

  const selectCase = (index: number) => {

    const firstCaseSelection = cases.every( cs => !cs.selected)

    if (firstCaseSelection) {
      setPlayerCase(index)
    }

    setCases(
      cases.map( (cs, idx) => {
        if (idx === index) {

          if (!cs.selected && audioRef && audioRef.current) {
            audioRef.current.play();
          }

          return { 
            ...cs,
            selected: !cs.selected
          }
        }

        return cs
      } )
    )
  };

  const takeDeal = () => {
    if (bankersOffer !== null) {
      setGameOver(true);
    }
  };

  const continuePlaying = () => {
    setBankersOffer(null);
  };

  return (
    <div>
      <h2>

      { playerCase !== null ?  `Maletn personal ${playerCase + 1}`: 'Selecciona tu maletin personal' }
      </h2>
      <Board
        cases={cases}
        playerCase={playerCase}
        onSelectCase={selectCase}
        gameOver={gameOver}
      />
      {cases.filter( (cs) => cs.selected ).length === 6 || cases.filter( (cs) => cs.selected ).length === 12 ? (
        <>
          <DealButton onClick={takeDeal} />
          <NoDealButton onClick={continuePlaying} />
        </>
      ) : null}
      {gameOver && <Result bankersOffer={bankersOffer} />}

      <SeeOptionsButton onClick={ () => setShowAvailableOpts(!avalableOptions)}/>

      
       {avalableOptions ? cases.map( cs =>{

        if (!cs.selected) {
          return (<div>{cs.prize}</div>)
        }
       } ) :null}
     
      <audio ref={audioRef} src={openChestSound} />
    </div>
  );
};

export default App