import React from 'react';
import { Typography, Paper } from '@mui/material';

interface CaseProps {
  value: number;
  selected: boolean;
  prize?: string;
  personalCase: number | null;
  onClick: () => void;
}

export const Case: React.FC<CaseProps> = ({ value, selected, prize, onClick, personalCase }) => {
  
  return (
    <Paper
      sx={{
        width: 150,
        height: 150,
        p: 5,
        m: 2,
        backgroundColor: selected ? (personalCase === (value -1) ? '#42a5f5' : '#ff9800') :   '#fff',
        cursor: selected ? 'not-allowed' : 'pointer',
      }}
      onClick={onClick}
    >
      <Typography variant="h5" component="div" color={selected ? 'text.primary' : 'text.secondary'}>
        {selected ? (personalCase === (value -1) ? 'Maletn Personal' : `${prize}`) :  `${value}`}
      </Typography>
    </Paper>
  );
};
